/*********** Function preloader ***********/

function masked(element, status) {
    if (status == true) {
        $('body').append('<div class="sc-loader-overlay d-flex justify-content-center align-items-center"><span class="sc-loader"></span></div>');
    } else {
        setTimeout(function () {
            $('.sc-loader-overlay').remove();
        }, 600);
    }
}

/*********** Function Show Location Map ***********/

function octShowMap(content, target) {

    var octMap = $(target);

    if (octMap.hasClass('not_in')) {
        octMap.html(content);
        octMap.removeClass('not_in');
    }

}

/*********** Function Show More Product in modules ***********/

function octShowMoreModule(module_id, lozadEnable = 0, button_id, html_module_id, page_id, path = 0) {
    let page = parseInt(document.querySelector("#" + page_id).value) + 1;

    $.ajax({
        type: 'post',
        dataType: 'html',
        url: 'index.php?route=extension/module/oct_product_modules/modulePage',
        data: 'module_id=' + module_id + '&page=' + page + '&oct_path=' + path,
        cache: false,
        beforeSend: function () {
            // enable loader
            masked('body', true);

            document.querySelector('#' + button_id).classList.add('oct-animated');
        },
        complete: function () {
            // check lozad
            if (lozadEnable == 1) {
                lozad('.oct-lazy').observe();
            }

            // remove loader
            masked('body', false);

            document.querySelector('#' + button_id).classList.remove('oct-animated');
        },
        success: function (octContent) {
            // parce octProducts
            let wrapper = document.createElement('div');
            wrapper.innerHTML = octContent;

            // write and write new products
            let productsToFind = wrapper.querySelectorAll('.sc-module-col');
            const productsArray = Array.from(productsToFind);
            let newProducts = document.querySelector("#" + html_module_id);
            productsArray.forEach(element => newProducts.insertAdjacentElement('beforeend', element));

            // check and write new button
            let buttonToFind = wrapper.querySelectorAll("#" + button_id);
            const buttonArray = Array.from(buttonToFind);

            document.querySelector("#" + page_id).value = page;
            // remove old button
            let checkButton = wrapper.querySelector(".oct-load-more-button-wrapper");

            if (checkButton === null) {
                document.querySelector('#' + html_module_id).nextElementSibling.classList.add('d-none');
            }
        }
    });
}

