/*********** Get User Cookie function ***********/

function getOCTCookie(name) {
    var matches = document.cookie.match(new RegExp("(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"));

    return matches ? decodeURIComponent(matches[1]) : 'undefined';
}

/*********** Notify function ***********/

function scNotify(type, text) {
    var iconType = 'info';
    switch (type) {
        case 'success':
            iconType = 'fas fa-check';
            break;
        case 'danger':
            iconType = 'fas fa-times';
            break;
        case 'warning':
            iconType = 'fas fa-exclamation';
            break;
    }
    $.notify({
        message: text,
        icon: iconType
    }, {
        type: type
    });
}

/*********** Mask function ***********/
function scInputMask(selector, mask) {
    $(selector).inputmask({
        'mask': mask
    });
}

/*********** Sidebar & menus ***********/
function scSidebar(title, type, showInnerSidebarType = 0) {
    if (!title && !type) return;

    var dataQuery, queryUrl, isMenu = 0;

    $('.sidebar-header-text').html(title);

    $('#sc_sidebar_content').html('<div></div>');

    switch (type) {
        // show main menu
        case 'menu':
            queryUrl = "index.php?route=octemplates/menu/oct_menu";
            isMenu = 1;
            dataQuery = 'mobile=1';
            break;

            // show locations
        case 'locations':
            $('#sc_sidebar_locations').prependTo('#sc_sidebar_content').removeClass('d-none');
            break;

            // show search
        case 'search':
            $('#search').prependTo('#sc_sidebar_content');
            break;
    }
    // main ajax query
    masked('#sidebar', true);
    $.ajax({
        type: "post",
        url: queryUrl,
        data: dataQuery,
        cache: false,
        success: function (data) {
            if (type !== 'locations' && type !== 'search') {
                $('#sc_sidebar_content').html(data);
            }

            let width = screen.width;

            masked('#sidebar', false);

            mobileMenu();

            if (isMenu === 1 && width < 768) {
                $("#language").prependTo("#oct_mobile_language");
                $("#currency").prependTo("#oct_mobile_currency");
            }

            switch (showInnerSidebarType) {
                case 'menu':
                    $("#mobile_menu_toggle_button").trigger("click");
                    break;

                case 'account':
                    $(".sidebar-main-menu-item-account").trigger("click");
                    break;

                case 'viewed':
                    $("#oct_sidebar_viewed_toggle").trigger("click").addClass('clicked');
                    break;
            }
        }
    });
}

$(function () {
    /*********** Category quantity ***********/
    $(document).on("click", ".sc-plus", function () {
        const oldVal = $(this).prev().val();
        var newVal = (parseInt($(this).prev().val(), 10) + 1);
        $(this).prev().val(newVal);
    });

    $(document).on("click", ".sc-minus", function () {
        const oldVal = $(this).next().val();
        const minimum = $(this).parent().find('.min-qty').val();
        if (oldVal > 1) {
            var newVal = (parseInt($(this).next().val(), 10) - 1);
        } else {
            newVal = 1;
        }
        if (newVal < minimum) {
            newVal = minimum;
        }
        $(this).next().val(newVal);
    });

    $(document).on('click', '.sc-module-cart-btn', function () {
        const productID = $(this).parent().find('input[name="product_id"]').val();
        const quantity = $(this).parent().find('.sc-module-quantity .form-control').val();

        let requiredOptions = document.querySelector('.required');

        if (requiredOptions && requiredOptions.classList.contains('option-required') && this.classList.contains('sc-product-fixed-cart-btn')) {
            cart.add(productID, quantity, 1);
        } else {
            cart.add(productID, quantity, 0);
        }
    });

    /*********** Fixed contacts ***********/

    $('#sc_fixed_contact_button').on('click', function () {
        $(this).toggleClass('clicked');
        $('.sc-fixed-contact-dropdown').toggleClass('expanded');
        $('.sc-fixed-contact-icon .fa-envelope, #back-top').toggleClass('d-none');
        $('.sc-fixed-contact-icon .sc-fixed-contact-text').toggleClass('d-none');
        $('#sc_fixed_contact_substrate').toggleClass('active');
    });

    $('#sc_fixed_contact_substrate').on('click', function () {
        $(this).removeClass('active');
        $('.sc-fixed-contact-dropdown').removeClass('expanded');
        $('.sc-fixed-contact-icon .fa-envelope, #back-top').removeClass('d-none');
        $('.sc-fixed-contact-icon .sc-fixed-contact-text').toggleClass('d-none');
        $('#sc_fixed_contact_button').removeClass('clicked');
    });

    $('.sc-fixed-contact-dropdown').click(function (e) {
        e.stopPropagation();
    });
    /*********** End of Fixed contacts ***********/

    $(document).ready(function () {

        $(document).on("click", "#oct_sidebar_viewed_toggle", function () {
            if (!$(this).hasClass('clicked')) {
                masked('#sidebar', true);
                $.ajax({
                    type: "post",
                    url: "index.php?route=octemplates/main/oct_functions/productViews",
                    cache: false,
                    success: function (data) {
                        masked('#sidebar', false);
                        $('#oct_sidebar_viewed').html(data);

                    }
                });
            }
        });

        var body = $('body');
        body.on('mouseover', '[data-bs-toggle="tooltip"]', function (e) {
            e.stopPropagation();
            return new bootstrap.Tooltip(this).show();
        });

        body.on('mouseleave', '[data-bs-toggle="tooltip"]', function (e) {
            $('[role="tooltip"]').fadeOut(function () {
                e.stopPropagation();
                $(this).remove();
            });
        });

        body.on('mouseover', '[data-bs-toggle="popover"]', function (e) {
            e.stopPropagation();
            return new bootstrap.Popover(this).show();
        });

        body.on('mouseleave', '[data-bs-toggle="popover"]', function (e) {
            $('[role="tooltip"]').fadeOut(function () {
                e.stopPropagation();
                $(this).remove();
            });
        });
    });
});

