/* Agree to Terms */
$(document).delegate('.agree', 'click', function (e) {
    e.preventDefault();
    masked('body', true);
    $('#modal-agree').remove();

    var element = this,
        link = '';
    var r = $(element).data('rel');

    if (r && r != 'undefined') {
        link = 'index.php?route=information/information/agree&information_id=' + r;
    } else {
        link = $(element).attr('href');
    }

    $.ajax({
        url: link,
        type: 'get',
        dataType: 'html',
        cache: false,
        success: function (data) {
            html = '<div class="modal fade" id="modal-agree" tabindex="-1" role="dialog" aria-labelledby="modal-agree" aria-hidden="true">';
            html += '  <div class="modal-dialog modal-dialog-centered wide">';
            html += '    <div class="modal-content">';
            html += '      <div class="modal-header p-4">';
            html += '        <h5 class="modal-title fsz-20 d-flex align-items-center justify-content-between">' + $(element).text() + '</h5>';
            html += '        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>';
            html += '      </div>';
            html += '      <div class="modal-body modal-body-agree p-4">' + data + '</div>';
            html += '    </div>';
            html += '  </div>';
            html += '</div>';

            $('body').append(html);
            masked('body', false);
            $('#modal-agree').modal('show');
        }
    });
});

// Autocomplete */
(function ($) {
    $.fn.autocomplete = function (option) {
        return this.each(function () {
            this.timer = null;
            this.items = new Array();

            $.extend(this, option);

            $(this).attr('autocomplete', 'off');

            // Focus
            $(this).on('focus', function () {
                this.request();
            });

            // Blur
            $(this).on('blur', function () {
                setTimeout(function (object) {
                    object.hide();
                }, 200, this);
            });

            // Keydown
            $(this).on('keydown', function (event) {
                switch (event.keyCode) {
                    case 27: // escape
                        this.hide();
                        break;
                    default:
                        this.request();
                        break;
                }
            });

            // Click
            this.click = function (event) {
                event.preventDefault();

                value = $(event.target).parent().attr('data-value');

                if (value && this.items[value]) {
                    this.select(this.items[value]);
                }
            }

            // Show
            this.show = function () {
                var pos = $(this).position();

                $(this).siblings('ul.dropdown-menu').css({
                    top: pos.top + $(this).outerHeight(),
                    left: pos.left
                });

                $(this).siblings('ul.dropdown-menu').show();
            }

            // Hide
            this.hide = function () {
                $(this).siblings('ul.dropdown-menu').hide();
            }

            // Request
            this.request = function () {
                clearTimeout(this.timer);

                this.timer = setTimeout(function (object) {
                    object.source($(object).val(), $.proxy(object.response, object));
                }, 200, this);
            }

            // Response
            this.response = function (json) {
                html = '';

                if (json.length) {
                    for (i = 0; i < json.length; i++) {
                        this.items[json[i]['value']] = json[i];
                    }

                    for (i = 0; i < json.length; i++) {
                        if (!json[i]['category']) {
                            html += '<li data-value="' + json[i]['value'] + '"><a href="#">' + json[i]['label'] + '</a></li>';
                        }
                    }

                    // Get all the ones with a categories
                    var category = new Array();

                    for (i = 0; i < json.length; i++) {
                        if (json[i]['category']) {
                            if (!category[json[i]['category']]) {
                                category[json[i]['category']] = new Array();
                                category[json[i]['category']]['name'] = json[i]['category'];
                                category[json[i]['category']]['item'] = new Array();
                            }

                            category[json[i]['category']]['item'].push(json[i]);
                        }
                    }

                    for (i in category) {
                        html += '<li class="dropdown-header">' + category[i]['name'] + '</li>';

                        for (j = 0; j < category[i]['item'].length; j++) {
                            html += '<li data-value="' + category[i]['item'][j]['value'] + '"><a href="#">&nbsp;&nbsp;&nbsp;' + category[i]['item'][j]['label'] + '</a></li>';
                        }
                    }
                }

                if (html) {
                    this.show();
                } else {
                    this.hide();
                }

                $(this).siblings('ul.dropdown-menu').html(html);
            }

            $(this).after('<ul class="dropdown-menu"></ul>');
            $(this).siblings('ul.dropdown-menu').delegate('a', 'click', $.proxy(this.click, this));

        });
    }
})(window.jQuery);

let octLoadMore = function (lozadEnable) {

    // get pages from pagination
    let pages = document.querySelectorAll('ul.pagination li').length;

    // get active page from pagination
    let active = parseInt(document.querySelector('ul.pagination li.active span').innerHTML);

    // find next page link
    let nextLi = document.querySelector('ul.pagination li.active').nextElementSibling;
    let nextPage = nextLi.outerHTML;
    let linkPattern = /.*href="(.*)".*/;
    let href = nextPage.replace(linkPattern, '$1');
    href = href.replace(/&amp;/g, '&');

    // ajax request
    $.ajax({
        url: href,
        dataType: 'html',
        beforeSend: function () {

            // enable loader
            masked('body', true);

            // add animation to button
            document.querySelector('.oct-load-more-button').classList.add('oct-animated');
        },
        complete: function () {

            // check lozad
            if (lozadEnable) {
                lozad('.oct-lazy').observe();
            }

            // remove loader
            masked('body', false);

            // check pagination
            octCheckPagination();

            // check dislay view
            octCheckDisplayView();

            switchCategoryDisplay();

            // remove animation from button
            document.querySelector('.sc-category .oct-load-more-button').classList.remove('oct-animated');
        },
        success: function (octContent) {

            // write to document new content
            octShowMoreContent(octContent);

            // push to history current pagination page
            history.pushState({}, '', href);
        }
    });
}

// Check pagination and hide main button
let octCheckPagination = function () {

    if (!document.querySelector('ul.pagination li.active + li')) {
        document.querySelector('.sc-category .oct-load-more-button, .sc-category .oct-load-more').classList.add('d-none');
        document.querySelector('.sc-category .oct-load-more').classList.remove('d-flex');
    } else {
        document.querySelector('.sc-category .oct-load-more-button').classList.remove('d-none');
    }

}

// Check display view
let octCheckDisplayView = function () {

    // remove old button
    let checkButton = document.querySelector(".sc-category-sort-limit");

    if (checkButton === null) {
        return;
    }

    if (localStorage.getItem('display') == 'list') {
        let listView = document.querySelector('#list-view');
        simulateClick(listView);
    } else if (localStorage.getItem('display') == 'grid') {
        let gridView = document.querySelector('#grid-view');
        simulateClick(gridView);
    } else {
        let priceView = document.querySelector('#price-view');
        simulateClick(priceView);
    }
}

// Show ajax content
let octShowMoreContent = function (octContent) {

    // parce pagination
    let wrapper = document.createElement('div');
    wrapper.innerHTML = octContent;
    let liToFind = wrapper.querySelectorAll('ul.pagination li');
    const ulArray = Array.from(liToFind);

    // write new pagination
    let pagination = document.querySelector('ul.pagination').innerHTML = '';
    let newPagination = document.querySelector('ul.pagination');
    ulArray.forEach(element => newPagination.insertAdjacentElement('beforeend', element));

    // parce octProducts
    let productsToFind = wrapper.querySelectorAll('.product-layout.sc-module-col');
    const productsArray = Array.from(productsToFind);

    // write new products
    let newProducts = document.querySelector('.sc-category-products');
    productsArray.forEach(element => newProducts.insertAdjacentElement('beforeend', element));
}

// Simulate click function
let simulateClick = function (elem) {

    // Create our event (with options)
    let evt = new MouseEvent('click', {
        bubbles: true,
        cancelable: true,
        view: window
    });

    // If cancelled, don't dispatch our event
    var canceled = !elem.dispatchEvent(evt);
};

$('body').on('click', '.product-layout.product-grid', function () {
    let product = this.getAttribute('data-pid');
    sessionStorage.setItem('productViewed', product);
});

$('body').on('click', '.product-layout.sc-module-col button', function (e) {
    let product = $(this).parent().parent().parent().parent().parent().attr('data-pid');
    sessionStorage.setItem('productViewed', product);
});

if (window.performance && window.performance.navigation.type == window.performance.navigation.TYPE_BACK_FORWARD) {

    if ('scrollRestoration' in history) {
        history.scrollRestoration = 'manual';
    }

    let scrollResetOnce = false;

    $(window).on("scroll", function () {
        if (scrollResetOnce) return;
        scrollResetOnce = true;
        scrollTo(0, 0);
    });


    let productInStorage = sessionStorage.getItem('productViewed');

    if (productInStorage !== null) {

        let offsetEl = document.querySelector('[data-pid="' + productInStorage + '"]');
        let toTop = offsetEl.offsetTop;

        if (window.innerWidth > 992) {
            toTop = toTop - 58;
        } else {
            toTop = toTop - 100;
        }

        $(window).on("pageshow", function (event) {
            $('html, body').animate({
                scrollTop: toTop
            }, '50');
            sessionStorage.removeItem('productViewed');
        });
    }

} else {
    sessionStorage.removeItem('productViewed');
    history.scrollRestoration = 'auto';
}

